@media screen and (max-width:1023px){
    .overlay{
        position: fixed;
        top:0;
        bottom: 0;
        left: 0;
        right: 0;
        background-color:#00000066;
    }
    .nav__mobile{
        position: fixed;
        top:0;
        left: 0;
        bottom: 0;
        background-image: linear-gradient(320deg, #FFE53B, #00FFFF);
        width: 100%;
        max-width: 320px;
        display: flex;
        transition: 0.5s;
        z-index: 99;
        box-shadow: 1px -1px 6px #888888;
    }
    .nav-mobile__list{
        list-style: none;
        width: 90%;
    }
    .nav-mobile__link{
        text-decoration: none;
        color: black;
        font-size: 25px;
        display: block;
        padding: 10px 0;
        font-family: cursive;
        text-align: center;
        cursor: pointer;
    }
    .nav-mobile__link:hover{
        color: red;
    }
}