.Contain{
  background-image: linear-gradient(215deg, #FFE53B, #00FFFF);
}
.Navbar{
  width: 100%;
  box-shadow: 1px -1px 6px #888888;
  display: flex;
  position: fixed;
  z-index: 99;
  top:0;
  background-image: linear-gradient(215deg, #FFE53B, #00FFFF);
}
.NavMenu{
  list-style: none;
  display: flex;
}
.NavMenu li{
  margin: auto 20px;
  cursor: pointer;
}
.NavOption{
  display: none;
}

.HeadingText{
  padding: 150px 0;
}
.typewrite{
  font-family:cursive;
}
.Btn{
  color: white;
  background-color:#1ab394;
  border: 1px solid white;
  padding: 10px 35px;
  cursor: pointer;
  border-radius: 10px;
  font-size: 15px;
  margin: 10px 15px;
  text-decoration: none;
}
.Btn:hover{
  background-color: #158871;
}
.Avatar{
  width: 100%;
  border-radius: 50%;
  font-size: 50%;
  margin: 60px 0;
  box-shadow: 1px -1px 6px #888888;
  border: 5px solid #1ab394 ;
}
.AboutMe{
  margin: 100px 0;
}
.AboutImg{
  width: 100%;
  border-radius: 20px;
}
.Information span{
  font-size: 25px;
}
.Infor{
  margin: 10px 0;
  display: flex;
}
.Infor-1{
  list-style: none;
  margin-left: -40px;
}
.Infor-1 li{
  font-size: 1rem;
  margin: 22px 0;
}
.Infor-1 span{
  font-size: 18px;
  color: gray;
}
.container {
  width: 100%;
  background-color: #ddd;
  margin: 10px 0;
  border-radius: 10px;
}

.skills {
  text-align: right;
  padding-top: 10px;
  padding-bottom: 10px;
  color: white;
}
.mapAddress{
  border-radius: 20px;
}

.html {width: 15%; background-color: #04AA6D;border-radius: 10px; animation-name: html;animation-duration: 3s; animation-fill-mode: forwards;}
.css {width: 15%; background-color: #2196F3;border-radius: 10px;animation-name: css;animation-duration: 3s; animation-fill-mode: forwards;}
.javascript {width: 15%; background-color: gold;border-radius: 10px;animation-name: javascript;animation-duration: 3s; animation-fill-mode: forwards;}
.react {width: 15%; background-color: lightskyblue;border-radius: 10px;animation-name: react;animation-duration: 3s; animation-fill-mode: forwards;}
.photoshop{width: 15%; background-color: red;border-radius: 10px;animation-name: photoshop;animation-duration: 3s; animation-fill-mode: forwards;}
@keyframes html {
  from {width: 15%;}
  to {width: 85%;}
}
@keyframes css {
  from {width: 15%;}
  to {width: 80%;}
}
@keyframes javascript {
  from {width: 15%;}
  to {width: 70%;}
}
@keyframes react {
  from {width: 15%;}
  to {width: 60%;}
}
@keyframes photoshop {
  from {width: 15%;}
  to {width: 50%;}
}
.MyPortfolio{
  color: #1ab394;
  margin-top: 113px;
}
.project img{
  width: 100%;
  border-radius: 20px;
  cursor: pointer;
  box-shadow: 1px -1px 6px #888888;
}
.project img:hover{
  opacity: 0.7;
}
.Contact-title{
  color:#1ab394;
  font-size: 20px;
  font-weight: bold;
}
.InputName{
  width: 99%;
  height: 45px;
  border-radius: 25px;
  padding-left: 15px;
  margin: 20px 0;
  font-size: 15px;
  color: #1ab394;
  border: 1px solid white;
}
.Message{
  padding-top: 10px;
  height: 200px;
  resize: none;
}
.btnToTop{
  background-color: lightgreen;
    font-size: 40px;
    border: 1px solid lightgreen;
    position: fixed;
    bottom: 20px;
    right: 30px;
    border-radius: 20px;
    cursor: pointer;
}


/* Tablet */
@media only screen and (min-width: 46.25em) and (max-width: 63.9375em) {
  .row{
      margin-left: 4px;
      margin-right: 4px;
  }
  .Navbar{
    position: fixed;
    z-index: 99;
  }

}
.NavMobile{
  display: none;
}

/* Mobile */
@media only screen and (max-width: 46.1875em) {
  .row{
      margin-left: 4px;
      margin-right: 4px;
  }
  .NavMobile{
    display: block;
  }  
  .NavMenu li{
    display: none;
  }
  .NavOption{
    display: block;
    margin: auto 0;
    font-size: 30px;
    cursor: pointer;
  }
  .HeadingText{
    margin: 0 auto;
    padding: 10px 0;
  }
  .Infor{
    display: flex;
    flex-direction: column;
  }
  .Navbar{
    position: fixed;
    z-index: 99;
  }
}
